
import { Checkbox } from '@material-ui/core';
import { type } from '@testing-library/user-event/dist/type';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Map, ZoomControl, MapTypeControl, MapMarker } from "react-kakao-maps-sdk";
import CompareMenuModal from './CompareMenuModal';
import MenuModal from './MenuModal';
import './SearchService.css';

const { kakao } = window;

function ServiceMain(){

    const [map, setMap] = useState(); //  :   
    const [markers, setMarkers] = useState([]) //    
    const [info, setInfo] = useState(); //  
    const [lat, setLat] = useState(); //   
    const [lng, setLng] = useState(); //   
    const [category, setCategory] = useState([]); //   
    const [people, setPeople] = useState(1); //   
    const [money, setMoney] = useState(-1); //   
    const [placeList, setPlaceList] = useState([]); //    ( Json ) []
    const [placeRender, setPlaceRender] = useState(); //   
    const [pageRender, setPageRender] = useState(); //  
    const [page, setPage] = useState([]); //    ( Json ) []
    const [selectPage, setSelectPage] = useState(0); //  
    const [showMarker, setShowMarker] = useState(); //  
    const [showMenu, setShowMenu] = useState([]); //     

    let start, end, claName; //   

    //    
    const [menuModalOpen, setMenuModalOpen] = useState(false);
    const openMenuModal = () => { setMenuModalOpen(true); };
    const closeMenuModal = () => { setMenuModalOpen(false); };
    
    //    
    const [compareModalOpen, setCompareModalOpen] = useState(false);
    const openCompareModal = () => { setCompareModalOpen(true); };
    const closeCompareModal = () => {
        setCompareModalOpen(false); 
        initCompareParam();
    };

    //     
    function initCompareParam(){
        let initTarget = JSON.parse(window.localStorage.getItem('ComparePlace'));
        document.getElementById(initTarget[0].id).style.background='';
        document.getElementById(initTarget[1].id).style.background='';
        window.localStorage.setItem('ComparePlace',JSON.stringify([]));
        setShowMenu([]);
    }
    
    //     
    useEffect(()=>{window.localStorage.setItem('ComparePlace',JSON.stringify([]));},[]);

    

    // ESC   
    window.onkeyup = function(e) {
        var key = e.keyCode ? e.keyCode : e.which;
        if(key == 27 && ( menuModalOpen || compareModalOpen )) { // ESC and (   )   
            closeMenuModal();
            closeCompareModal();
        }
    }

    //  
    const api = axios.create({
        baseURL : 'http://1.209.148.228:2221/api',
        //  
        credentials: true, 
    });
    

    //   
    useEffect(() => {
        if (!map) return //       
        setLat(map.getCenter().getLat());
        setLng(map.getCenter().getLng());
        kakao.maps.event.addListener(map, 'dragend', () => {
            setLat(map.getCenter().getLat());
            setLng(map.getCenter().getLng());
            setInfo({
                center: {
                    lat: map.getCenter().getLat(),
                    lng: map.getCenter().getLng(),
                }})
            })
            // console.log('   ');
    }, [map])



    //       
    useEffect(() => {
        window.localStorage.setItem('SearchPlace',JSON.stringify(placeList));
        if(placeList==null){
            //   
            setPlaceRender(<div className='getNull'>  </div>);
            console.log('   ( X    ) ');

        }else{
            RenderSearchList();
        }
    },[placeList])


    //  API
    function requestPlaceList(e){
        e.preventDefault();
        console.log('[, , , , ] : ',category,people,money,lat,lng);
        api.post('/place/search/around',{
            category : category,
            people : people,
            money : money,
            lat : lat,
            lng : lng
            //   -1 ,       
        })
        .then(Result=>{
            // console.log(Result.data);
            setPlaceList(Result.data.map(place => {
                return({
                    id : place['place'].id,
                    address : place['place'].address,
                    category : place['place'].category,
                    id : place['place'].id,
                    img : place['place'].img,
                    lat : place['place'].lat,
                    lng : place['place'].lng,
                    name : place['place'].name,
                    tel : place['place'].tel,
                    price : parseInt(place['averPrice'])
                });
            }));
            // console.log('  : ',placeList);
        }).catch((Error)=>{
            console.log(Error);
            alert('  .\n   ');
        })
    }


    //    ->   
    function updateCategory(e){
        let cat = e.target.value;
        if(category.includes(cat)){
            category.splice(category.indexOf(cat),1);
            
        }else{
            setCategory([...category, cat]);
        }
        // console.log(' : ',category);

    }


    //   
    //   
    //   
    function RenderSearchList(page=1){
        if(!map) return;
        setSelectPage(page);

        let getPlaceList = JSON.parse(window.localStorage.getItem('SearchPlace')); //  
        let placeSize = Math.ceil(getPlaceList.length/15); //   
        if(page > placeSize) page = placeSize ; //       
        getPlaceList = getPlaceList.slice((page-1)*14,page*14+1); //     

        
        //  
        const bounds = new kakao.maps.LatLngBounds()
        let markers = []

        //  
        setPlaceRender(getPlaceList.map(place => {
            markers.push({
                position: {
                    lat: place.lat,
                    lng: place.lng,
                },
                content: place.name,
            })
            bounds.extend(new kakao.maps.LatLng(place.lat,place.lng)); //    
            return(
                    <li className='searchPlace' key={place.id}>
                        <div className='placeImg'>
                            <img src={place.img?place.img:'/img/altimg.png'} alt='  '></img>
                        </div>

                        <div className='placeInfo'>
                            <div className='placeName'>{place.name}</div>
                            <div className='placeAddr'>{place.address}</div>
                            <div className='placeTel'>{place.tel}</div>
                            { place.price > 0 && <div className='placePrice'> : {place.price}</div> }
                        </div>

                        <div className='placeAction'>
                            <button className='btn' onClick={(e) => {requestMenu([place.id],[place.name]);}}></button>
                            <button className='btn' id={place.id} onClick={()=>{addCompareTarget(place.id, place.name);}} ></button>
                        </div>
                    </li>
                );
            }
        )); // setPlaceRender
        setMarkers(markers);
        getPlaceList.length > 0 && RenderPage(page,placeSize); //   :  1   
    } // RenderSearchList


    //    
    function RenderPage(sel = 1, total = 1){
        if( sel == 0 ) return;

        //    ->   : 1   1 
        if(sel%5 == 0){ start = sel - 4 }
        else{ sel < 1 ? start = 1 : start = sel - sel%5 + 1; }
        start+4 >= total ? end = total : end = start + 4; //   :          

        //      
        setPageRender(
            [...Array(end+1).keys()].splice(start).map(i=>{
                sel == i ? claName = 'pagination-link is-current' : claName = 'pagination-link';
                return(
                    <li key={i}><a className={claName} onClick={()=>{RenderSearchList(i);}} >{i}</a></li>
                );
            }
        )); // function/map/render        
    } // RenderPage


    //  
    function requestMenu(placeId,placeName){
        // console.log('  : ',placeId,' / ', placeName);
        api.post('/place/search/menu',placeId)
        .then(Result=>{
            let responseMenu = Result.data;
                //   
                // category: ""
                // menuKey: {id: '', name: ''}
                // place_category: ""
                // price: 
            // console.log('   : ',responseMenu);
            if(responseMenu.length < 1){
                alert('    ..!');
                if(JSON.parse(window.localStorage.getItem('ComparePlace')).length > 1){
                    initCompareParam();
                }
                return;
            }

            // PlaceName length   
            if(placeId.length == 2){ // ID 2 ,   ->  
                let defMenu = [[],[]];
                let defTarget;
                responseMenu.map(menu => {
                    menu.menuKey.id == placeId[0] ? defTarget = 0 : defTarget = 1;
                    // console.log(' ID /  ->  : ',menu.menuKey.id,' / ',placeId[0].id,' -> ',defTarget)
                    defMenu[defTarget] = [...defMenu[defTarget],{ name : menu.menuKey.name, price : menu.price}];
                })
                // console.log('  : ',defMenu);
                setShowMenu([
                    {
                        id : placeId[0],
                        name : placeName[0],
                        menu : defMenu[0]
                    },
                    {
                        id : placeId[1],
                        name : placeName[1],
                        menu : defMenu[1]
                    }
                ]);

                // 
                openCompareModal();


            }else{ // ID 1    ->  
                console.log('   : ',responseMenu);
                setShowMenu(responseMenu.map(menu => {
                    return({
                        placeName : placeName,
                        placeId : placeId,
                        category : menu.category,
                        name : menu.menuKey.name,
                        price : menu.price,
                    });
                }));
                openMenuModal();
            }
        }).catch((Error)=>{
            console.log(Error);
            alert('   !!',Error);
        })
    }

// useEffect(()=>{console.log('    : ',showMenu);},[showMenu]);

    //    
    //!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!    !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    function addCompareTarget(placeId, placeName){
        let target = document.getElementById(placeId);
        let ComparePlace = JSON.parse(window.localStorage.getItem('ComparePlace')); //   
        // console.log('    : ',ComparePlace);
        
        //    
        if (ComparePlace.length == 0 ){
            ComparePlace.push({id : placeId, name : placeName});
            window.localStorage.setItem('ComparePlace', JSON.stringify(ComparePlace)); // 
            target.style.background = 'blue'; //  
            // console.log('  ');
            return; 
        }


        //  1  
        for(let i = 0 ; i < ComparePlace.length ; i++){
            // console.log(' : ',i,' / ',ComparePlace.length);
           if(ComparePlace[i].id == placeId) { //   ID 
                ComparePlace.splice(i,1); // 
                window.localStorage.setItem('ComparePlace', JSON.stringify(ComparePlace));
                target.style.background = ''; //  
                // console.log('  ');
                return; 
            }//else{console.log('X');}
           if(i == ComparePlace.length-1){ //    
                // console.log(' : ',i);
                if(ComparePlace.length > 1){ //  2  
                    alert(' 2..!');
                    console.log(' 2..!');
                    return;
                }
                ComparePlace.push({id : placeId, name : placeName});
                window.localStorage.setItem('ComparePlace', JSON.stringify(ComparePlace)); // 
                target.style.background = 'blue'; //  
                // console.log('  ');
                break;
            }
            // console.log('   ..   ');
        }


        //       
        let param = JSON.parse(window.localStorage.getItem('ComparePlace'));
        if(param.length == 2){
            // console.log(' 2  ->    !');
            requestMenu( [param[0].id, param[1].id], [param[0].name, param[1].name] ) //   
        }


        // if(ComparePlace.id.includes(placeId)){ //  ID   
        //     ComparePlace.splice(ComparePlace.indexOf(placeId),1); //   
        //     console.log('   : ',ComparePlace);
        //     target.style.background = 'white'; //  
        //     window.localStorage.setItem('ComparePlace',ComparePlace); //   
        // }else{ //  ID  
        //     if(ComparePlace.length == 2){ //  2       
        //         alert('  2   ..!');
        //         console.log('  2   ..!');
        //         return;
        //     }
        //     window.localStorage.setItem('ComparePlace',[...ComparePlace, placeId]); //  ID 
        //     target.style.background = 'green'; //  
        // }

        // //      2  ->   
        // if(window.localStorage.getItem('ComparePlace').length == 2){
        //     console.log('  ..!');
        //     requestMenu(window.localStorage.getItem('ComparePlace'),'')
        //     openCompareModal();
        // }

        // let placeId = e.target.value;
        // let target = document.getElementById(placeId);
        // if(compareMenu.length > 2){ // 2    
        //     alert('  2 ..!');
        //     console.log('  2 ..!');
        //     return; 
        // } 
        // // console.log(placeId,'    : ',compareMenu);
        // // setCompareMenu([placeId]);
        // // console.log('  ->', compareMenu);
        // if(compareMenu.includes(placeId)){ //   ,  
        //     compareMenu.splice(compareMenu.indexOf(placeId),1);
        //     console.log('   : ',compareMenu);
        //     target.style.background = 'white';
        // }else{
        //     setCompareMenu([placeId]);
        //     console.log('   : ',compareMenu);
        //     target.style.background = 'green';
        // }
    }

    // useEffect(()=>{
    //     console.log('   : ',compareMenu);
    //     if(compareMenu.length > 1){
    //         console.log('2  ..!');
    //     }
    // },[compareMenu])

    //   ->     -> //  ->   -> 
    return (
        <>
            {/*         ->  :    */}
            <Map center={{lat: 37.56048643647845, lng:126.83786575805817 }}
                style={{width: '100%', height: '100%'}}
                level={3}  onCreate={(map) => setMap(map)} >    
                <ZoomControl position={kakao.maps.ControlPosition.TOPRIGHT} />
                <MapTypeControl position={kakao.maps.ControlPosition.TOPRIGHT}/>

                {/*   */}
                {markers.map((marker) => (
                        <MapMarker
                            key={`marker-${marker.content}-${marker.position.lat},${marker.position.lng}`}
                            position={marker.position} onClick={() => setInfo(marker)}>
                            {info &&info.content === marker.content && (
                                <div style={{color:"#000"}}>{marker.content}</div>
                            )}
                        </MapMarker>
                    
                ))}

                {/*      */}
                <div id="menu_wrap" style={{width: '400px', top : '10px'}}> {/*  */}

                    {/*   */}
                    <div className="searchForm">
                        <div>
                            <form onSubmit={requestPlaceList}>
                                <p></p>
                                <label><input type='checkbox' name='category' value='kor' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='ch' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='jp' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='wf' onChange={updateCategory}></input> </label><br/>
                                <label><input type='checkbox' name='category' value='mt' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='sb' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='pz' onChange={updateCategory}></input> </label><br/>
                                <label><input type='checkbox' name='category' value='ck' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='ff' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='br' onChange={updateCategory}></input> </label>
                                <label><input type='checkbox' name='category' value='cf' onChange={updateCategory}></input> </label>
                                <details>
                                    <summary> </summary>
                                    <div className='people'>
                                        <p></p>
                                        <input type='range' name='people' id='people' value={people} min='1' max='10' onChange={(e)=>{setPeople(e.target.value)}}/>
                                            &nbsp;{people}
                                    </div>
                                    <div className='money'>
                                        <p></p>
                                        <input type='range' name='money' id='money' value={money} min='10000' max='150000' step='1000' onChange={(e)=>{setMoney(e.target.value)}}/>
                                            &nbsp;{money > 0 ? money+'' : '  '}
                                    </div>

                                </details>

                                <button type='submit' className='button is-primary' style={{width : '100%',height: '40px', fontSize:'18px'}}></button> 
                            </form>
                        </div>
                    </div>
                    <hr/>

                    {/*  */}
                    <ul id="placesList">
                        {placeRender}
                    </ul>

                    {/*  */}
                    <div id="pagination">
                        {/*         -> { pagenationa ? <></> : <pagenation /> } */}
                        <nav className="pagination is-centered" role="navigation" aria-label="pagination">
                            <span>
                                { selectPage > 1 ? <a className="pagination-previous" onClick={()=>{RenderSearchList(selectPage-1);}}> {'<'} </a> : <a style={{width : '40px'}}>&nbsp;</a>}
                            </span>
                            <span>
                                <ul className="pagination-list">
                                    {pageRender}
                                </ul>
                            </span>
                            <span>
                                { selectPage != 0 && (
                                    selectPage < placeList.length/15 ? <a className="pagination-next" onClick={()=> {RenderSearchList(selectPage+1);}}> {'>'} </a> : <a style={{width : '40px'}}>&nbsp;</a>
                                )}
                            </span>
                        </nav>
                    </div>

                    {/*   */}
                    <MenuModal open={menuModalOpen} close={closeMenuModal} menuList={showMenu} >
                    </MenuModal>

                    {/*    */}
                    <CompareMenuModal open={compareModalOpen} close={closeCompareModal} menuList={showMenu} >
                    </CompareMenuModal>
                </div>
            </Map>
        </>
    );
}

export default ServiceMain;


