import React, {
  ReactNode,
  Ref,
  useImperativeHandle,
  useLayoutEffect,
  useMemo,
} from "react"
import useMap from "../hooks/useMap"

export const DrawingManagerContext =
  React.createContext<kakao.maps.drawing.DrawingManager>(
    undefined as unknown as kakao.maps.drawing.DrawingManager
  )

export type DrawingManagerProps<
  T extends kakao.maps.drawing.OverlayType = kakao.maps.drawing.OverlayType
> = Partial<kakao.maps.drawing.OverlayOptions> &
  Pick<kakao.maps.drawing.OverlayOptions, `${T}Options`> & {
    /**
     *       . draw, drag, edit 3    (:   )
     *  [draw]      
     */
    guideTooltip?: Array<"draw" | "drag" | "edit">
    /**
     *     (:   )
     */
    drawingMode?: Array<T>

    /**
     *    .
     */
    onSelect?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *   .
     */
    onDrawstart?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *   ,   .
     */
    onDraw?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *   .
     */
    onDrawend?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *     . (Polyline, Polygon, Arrow )
     */
    onDrawnext?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *   .
     */
    onCancle?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *    .
     */
    onRemove?: (event: kakao.maps.drawing.MouseEvent) => void

    /**
     *     .
     *   ///  undo  redo    .
     */
    onStateChanged?: () => void

    /**
     * Toolbox    .
     */
    children?: ReactNode
  }

/**
 *      .
 *
 *    Props         .
 *
 * > `on~`   props     
 *
 * DrawingManager  Map, Marker, CustomOverlay  Props       DrawingManager     .
 *
 *   ref   `DrawingManager`     ,        .
 *
 * TypeScript   `Generic`  , `ref`   typing  `drawingMode`    .
 *
 * ```tsx
 * const Main = () => {
 *     const managerRef = useRef<kakao.maps.drawing.DrawingManager<
 *       kakao.maps.drawing.OverlayType.POLYLINE
 *     >>(null);
 *
 *     function selectOverlay(type: string) {
 *       const manager = managerRef.current;
 *       manager.cancel();
 *       manager.select(kakao.maps.drawing.OverlayType.POLYLINE);
 *     }
 *
 *     return (
 *       <>
 *         <Map
 *           center={{
 *             //  
 *             lat: 33.450701,
 *             lng: 126.570667,
 *           }}
 *           style={{
 *             width: "100%",
 *             height: "450px",
 *           }}
 *           level={3} //   
 *         >
 *           <DrawingManager
 *             ref={managerRef}
 *             drawingMode={[
 *               kakao.maps.drawing.OverlayType.POLYLINE,
 *             ]}
 *             guideTooltip={['draw', 'drag', 'edit']}
 *             polylineOptions={{
 *               draggable: true,
 *               removable: true,
 *               editable: true
 *             }}
 *           />
 *         </Map>
 *         <button onClick={(e) => {
 *           selectOverlay('POLYLINE')
 *         }}></button>
 *       </>
 *     )
 *   }
 * ```
 *
 * > JavaScript  
 *
 * ```jsx live
 * function() {
 *   const Main = () => {
 *     const managerRef = useRef(null);
 *
 *     function selectOverlay() {
 *       const manager = managerRef.current;
 *       manager.cancel();
 *       manager.select(kakao.maps.drawing.OverlayType.POLYLINE);
 *     }
 *
 *     return (
 *       <>
 *         <Map
 *           center={{
 *             //  
 *             lat: 33.450701,
 *             lng: 126.570667,
 *           }}
 *           style={{
 *             width: "100%",
 *             height: "450px",
 *           }}
 *           level={3} //   
 *         >
 *           <DrawingManager
 *             ref={managerRef}
 *             drawingMode={[
 *               kakao.maps.drawing.OverlayType.POLYLINE,
 *             ]}
 *             guideTooltip={['draw', 'drag', 'edit']}
 *             polylineOptions={{
 *               draggable: true,
 *               removable: true,
 *               editable: true
 *             }}
 *           />
 *         </Map>
 *         <button onClick={selectOverlay}></button>
 *       </>
 *     )
 *   }
 *   return (<Main />)
 * }
 * ```
 */
const DrawingManager = React.forwardRef(function <
  T extends kakao.maps.drawing.OverlayType
>(
  {
    arrowOptions,
    circleOptions,
    ellipseOptions,
    markerOptions,
    polygonOptions,
    polylineOptions,
    rectangleOptions,
    drawingMode,
    guideTooltip,
    onSelect,
    onDrawstart,
    onDraw,
    onDrawend,
    onDrawnext,
    onCancle,
    onRemove,
    onStateChanged,
    children,
  }: DrawingManagerProps<T>,
  ref: Ref<kakao.maps.drawing.DrawingManager<T>>
) {
  const map = useMap("Toolbox")

  const drawingManager = useMemo(
    () =>
      // @ts-ignore
      new kakao.maps.drawing.DrawingManager({
        map,
        drawingMode,
        guideTooltip,
        arrowOptions,
        circleOptions,
        ellipseOptions,
        markerOptions,
        polygonOptions,
        polylineOptions,
        rectangleOptions,
      }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  )
  useImperativeHandle(ref, () => drawingManager, [drawingManager])

  useLayoutEffect(() => {
    onSelect && drawingManager.addListener("select", onSelect)
  }, [drawingManager, onSelect])

  useLayoutEffect(() => {
    onDrawstart && drawingManager.addListener("drawstart", onDrawstart)
  }, [drawingManager, onDrawstart])

  useLayoutEffect(() => {
    onDraw && drawingManager.addListener("draw", onDraw)
  }, [drawingManager, onDraw])

  useLayoutEffect(() => {
    onDrawend && drawingManager.addListener("drawend", onDrawend)
  }, [drawingManager, onDrawend])

  useLayoutEffect(() => {
    onDrawnext && drawingManager.addListener("drawnext", onDrawnext)
  }, [drawingManager, onDrawnext])

  useLayoutEffect(() => {
    onCancle && drawingManager.addListener("cancel", onCancle)
  }, [drawingManager, onCancle])

  useLayoutEffect(() => {
    onRemove && drawingManager.addListener("remove", onRemove)
  }, [drawingManager, onRemove])

  useLayoutEffect(() => {
    onStateChanged &&
      drawingManager.addListener("state_changed", onStateChanged)
  }, [drawingManager, onStateChanged])

  return (
    <DrawingManagerContext.Provider value={drawingManager}>
      {children}
    </DrawingManagerContext.Provider>
  )
}) as unknown as <
  T extends kakao.maps.drawing.OverlayType = kakao.maps.drawing.OverlayType
>(
  _props: DrawingManagerProps<T> &
    React.RefAttributes<kakao.maps.drawing.DrawingManager<T>>
) => null

export default DrawingManager
