const { kakao } = window;
let map;
export default function KakaoMapScript() {
    console.log('  ');
    var markers = [];
    const container = document.getElementById('myMap');
    const options = {
        center: new kakao.maps.LatLng(126.83773640693936, 37.558677743241596),
        level: 3
    };
    map = new kakao.maps.Map(container, options);


///////////////////////////////////////////////////[ ]//////////////////////////////////////////////////////////
    //    
    var ps = new kakao.maps.services.Places();  

    //          
    var infowindow = new kakao.maps.InfoWindow({zIndex:1});

    //           
    var mapTypeControl = new kakao.maps.MapTypeControl();

    //     
    // kakao.maps.ControlPosition     TOPRIGHT   
    map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

    //          
    var zoomControl = new kakao.maps.ZoomControl();
    map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


///////////////////////////////////////////////[  ]/////////////////////////////////////////////////////
    //  
    var marker = new kakao.maps.Marker({map : map});

    //       
    kakao.maps.event.addListener(map, 'tilesloaded', displayMarker);

    function displayMarker() {
        
        //     
        marker.setPosition(map.getCenter()); 
        marker.setMap(map); 

        //            
        //       
        // kakao.maps.event.removeListener(map, 'tilesloaded', displayMarker);
    }
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


////////////////////////////////////////////////////[ ]///////////////////////////////////////////////////////
    //   
    searchPlaces();

    //    
    function searchPlaces() {

        var keyword = document.getElementById('keyword').value;
        //  if
        console.log(' : '+keyword)

        if (!keyword.replace(/^\s+|\s+$/g, '')) {
            alert(' !');
            return false;
        }

        //      
        ps.keywordSearch( keyword, placesSearchCB, {
            radius : 2000,
            location: new kakao.maps.LatLng(map.getCenter().getLat(),map.getCenter().getLng())
        }); 
    }

    //      
    function placesSearchCB(data, status, pagination) {
        if (status === kakao.maps.services.Status.OK) {

            //   
            //    
            displayPlaces(data);
            console.log(data)

            //   
            displayPagination(pagination);

        } else if (status === kakao.maps.services.Status.ZERO_RESULT) {

            alert('   .');
            return;

        } else if (status === kakao.maps.services.Status.ERROR) {

            alert('    .');
            return;

        }
    }
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


////////////////////////////////////////////////[   ]//////////////////////////////////////////////////
    //      
    function displayPlaces(places) {

        var listEl = document.getElementById('placesList'), 
        menuEl = document.getElementById('menu_wrap'),
        fragment = document.createDocumentFragment(), 
        bounds = new kakao.maps.LatLngBounds(), 
        listStr = '';
        
        //      
        removeAllChildNods(listEl);

        //     
        removeMarker();
        
        for ( var i=0; i<places.length; i++ ) {

            //    
            var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
                marker = addMarker(placePosition, i), 
                itemEl = getListItem(i, places[i]),
                address = places[i].road_address_name,
                place_name = places[i].place_name,
                phone = places[i].phone,
                place_id = places[i].id;
                
                //       
                // LatLngBounds   
            bounds.extend(placePosition);

            //    mouseover 
            //     
            // mouseout    
            (function(marker, title) {
            
            //    
            //      CSS   
            
            //      
            kakao.maps.event.addListener(marker, 'click', function() {
                var content = `
                                <div class="wrap" id="infoOverlay_${i}">
                                    <div class="info" id=>
                                        <div class="title" style="margin-bottom:0px;">
                                            ${place_name}
                                            <div class="close" onClick="closeOverlay('infoOverlay_${i}')" title=""></div> 
                                        </div>
                                        <div class="body">
                                            <div class="img">
                                                <img src="https://cfile181.uf.daum.net/image/250649365602043421936D" width="73" height="70"/>
                                            </div>
                                            <div class="desc">
                                                <div class="ellipsis">${address}</div>
                                                <div class="overlayPhone">${phone}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>`;

                var overlay = new kakao.maps.CustomOverlay({
                content: content,
                map: map,
                position: marker.getPosition()       
                });
                // overlay.setMap(map);

            });
            
            //       
                // kakao.maps.event.addListener(marker, 'click', function() {
                //     displayInfowindow(marker, title);
                // });
                
                //  
                // itemEl.onmouseover =  function () {
                //     displayInfowindow(marker, title);
                // };

                // itemEl.onmouseout =  function () {
                //     infowindow.close();
                // };
            })(marker, places[i].place_name);

            fragment.appendChild(itemEl);
        }

        //     Element 
        listEl.appendChild(fragment);
        menuEl.scrollTop = 0;

        //       
        map.setBounds(bounds);
    }

    //   Element   :  
    function getListItem(index, places) {

        var el = document.createElement('li'),
        itemStr = '<span class="markerbg marker_' + (index+1) + '"></span>' +
                    '<div class="info">' +
                    '   <h5>' + places.place_name + '</h5>';

        if (places.road_address_name) {
            itemStr += '    <span>' + places.road_address_name + '</span>' +
                        '   <span class="jibun gray">' +  places.address_name  + '</span>';
        } else {
            itemStr += '    <span>' +  places.address_name  + '</span>'; 
        }    
        itemStr += '  <span class="tel">' + places.phone  + '</span>' +
                    '</div>';           

        el.innerHTML = itemStr;
        el.className = 'item';

        return el;
    }
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


/////////////////////////////////////////////////[   ]//////////////////////////////////////////////////
    //       
    function addMarker(position, idx, title) {
        var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', //   url,   
            imageSize = new kakao.maps.Size(36, 37),  //   
            imgOptions =  {
                spriteSize : new kakao.maps.Size(36, 691), //   
                spriteOrigin : new kakao.maps.Point(0, (idx*46)+10), //       
                offset: new kakao.maps.Point(13, 37) //      
            },
            markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
                marker = new kakao.maps.Marker({
                position: position, //  
                image: markerImage 
            });

        marker.setMap(map); //    
        markers.push(marker);  //    

        return marker;
    }
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////



//////////////////////////////////////////////////[   ]//////////////////////////////////////////////////
    //       
    function removeMarker() {
        for ( var i = 0; i < markers.length; i++ ) {
            markers[i].setMap(null);
        }   
        markers = [];
    }

    //      
    function displayPagination(pagination) {
        var paginationEl = document.getElementById('pagination'),
            fragment = document.createDocumentFragment(),
            i; 

        //    
        while (paginationEl.hasChildNodes()) {
            paginationEl.removeChild (paginationEl.lastChild);
        }

        for (i=1; i<=pagination.last; i++) {
            var el = document.createElement('a');
            el.href = "#";
            el.innerHTML = i;

            if (i===pagination.current) {
                el.className = 'on';
            } else {
                el.onclick = (function(i) {
                    return function() {
                        pagination.gotoPage(i);
                    }
                })(i);
            }
            fragment.appendChild(el);
        }
        paginationEl.appendChild(fragment);
    }
    
    //    Element  
    function removeAllChildNods(el) {   
        while (el.hasChildNodes()) {
            el.removeChild (el.lastChild);
        }
    }
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


/////////////////////////////////////////////////[  ]///////////////////////////////////////////////////
    //   ,   
    kakao.maps.event.addListener(map, 'dragend', function() {        
        
        //    
        var latlng = map.getCenter(); 
        
        var message = '   ' + latlng.getLat() + ' , ';
        message += ' ' + latlng.getLng() + ' ';
        
        // var resultDiv = document.getElementById('result');  
        // resultDiv.innerHTML = message;
        
    });
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

}

//////////////////////////////////////////////////[  ]////////////////////////////////////////////////////
    //      

    var currentTypeId;

    //    
    export function setOverlayMapTypeId(maptype){
        var changeMaptype;
        
        // maptype     
        if (maptype === 'traffic') {            
            //  
            changeMaptype = kakao.maps.MapTypeId.TRAFFIC;     
        } else if (maptype === 'roadview') {        
            //   
            changeMaptype = kakao.maps.MapTypeId.ROADVIEW;    
        }
        
        //      
        if (currentTypeId) {
            map.removeOverlayMapTypeId(currentTypeId);    
        }
        
        // maptype    
        map.addOverlayMapTypeId(changeMaptype);
        
        //    
        currentTypeId = changeMaptype;  
        return 0;      
    }
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

