// import { MenuList } from '@material-ui/core';
import React, { useState } from 'react';
import './CompareMenuModal.css';

const CompareMenuModal = (props) => {
    const { open, close, menuList } = props;
    // console.log('[]   -> ',menuList);
    
    const [result, setResult] = useState([0,0]);
    const [result1, setResult1] = useState(0);
    const [result2, setResult2] = useState(0);

    if(!open) return;

    const renderMenu1 = menuList[0].menu.map(menu=>{
        return(
            <div className='menuInfo'>
                <div className='menuName'>
                    {menu.name}
                </div>
                <div className='menuPrice'>
                    <label>
                        {menu.price}  <input type='checkbox' name='0' id={menu.name} onChange={(e)=>{changeCalc(e); setResult1(result[0]);}} value={menu.price} ></input>
                    </label>
                </div>
            </div>
        );
    });

    const renderMenu2 = menuList[1].menu.map(menu=>{
        return(
            <div className='menuInfo'>
                <div className='menuName'>
                    {menu.name}
                </div>
                <div className='menuPrice'>
                    <label>
                        {menu.price}  <input type='checkbox' name='1' id={menu.name} onChange={(e)=>{changeCalc(e); setResult2(result[1])}} value={menu.price} ></input>
                    </label>
                </div>
            </div>
        );
    });

    function changeCalc(e){
        let price = parseInt(e.target.value); // 
        let targetIndex = parseInt(e.target.name); //   ( 0 / 1 )
        let calc = result; //    
        console.log('  /   /    => ',price,' / ',targetIndex,' / ',calc)
        if(e.target.checked){ // 
            calc[targetIndex] += price;  
        }else{ // 
            calc[targetIndex] -= price;
        }

        setResult(calc)
        console.log(' --> : ',result);

    }


    return (
    //   
    <div className={open ? 'openCompareModal modal' : 'modal'}>
        {open ? (
        <section className='compareForm'>
            <header>
                 
                <button className='close' onClick={close}>&times;</button>
            </header>
            <main className='compareMain'>
                <div style={{width:'100%'}}>
                    <div className='compareList'> {/*    */}
                        <div className='comparePlace rightPartition'> {/*   */}
                            <div className='comparePlaceName'>{menuList[0].name}</div> {/*   */}
                            <div className='comparePlaceMenu'>
                                {renderMenu1}
                            </div> {/*   ->     */}
                        </div>

                        <div className='comparePlace'> {/*   */}
                            <div className='comparePlaceName'>{menuList[1].name}</div> {/*   */}
                            <div className='comparePlaceMenu'>
                                {renderMenu2}
                            </div> {/*   ->     */}
                        </div> 
                    </div>
                </div>

                <div className='clacPlace'> {/*       */}
                    <div className='eachResult rightPartition'>
                        <span className='floatLeft ' style={{fontSize:'15pt'}} ></span>
                        <span style={{fontSize:'15pt', fontWeight:'bold'}}>{result1}</span> {/*    */}
                    </div>
                    <div className='eachResult'>
                        <span className='floatLeft' style={{fontSize:'15pt'}}></span>
                        <span style={{fontSize:'15pt', fontWeight:'bold'}}>{result2}</span> {/*    */}
                    </div>
                </div>

                
                <div className='compareResult'> {/*   */}
                    {/* <div> </div> */}
                        <span style={{fontWeight:'bold',}}>{result1 >= result2 ? menuList[1].name : menuList[0].name}</span>()&nbsp;
                        <span style={{fontWeight:'bold',}}>{Math.abs(result1-result2)}</span>  !&nbsp;&nbsp;
                    {/* <div> </div>  */}
                </div>

            </main>
            <footer></footer>
        </section>
        ) : null}
    </div>
    );
};

export default CompareMenuModal;


